import React, { useEffect } from 'react';
import './app.css';

export const App = () => {
  useEffect(() => {
    cast.framework.CastReceiverContext.getInstance().start();
  }, []);

  return (
    <div className="app">
      <div className="app__wrapper">
        <h1 className="app__heading">Are you receiving some couscous?</h1>
      </div>
    </div>
  );
}

